import Vue from "vue";
import VueRouter from "vue-router";
import { store } from "./store.js";

Vue.use(VueRouter);
Vue.component("private", () => import("../views/layouts/private"));
Vue.component("public", () => import("../views/layouts/public"));

const patternMatch = {
  authenticationView: "sign-in|sign-up|forgot-password|verify-password|verify-sign-up",
  blogTypes: "home|about|services|team|contact",
  userTypes: "backoffice|studio|service|client",
  productTypes: "products|packages|product-packages",
  id: "[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}|new",
};

export const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      beforeEnter(to, from, next) {
        let authenticated = store.getters.authenticated;
        if (authenticated) {
          next({ name: "dashboards" });
        } else {
          next({ name: "blogs", params: { blogType: "home" }});
        }
      },
    },
    {
      name: "authentication",
      path: `/:authenticationView(${patternMatch.authenticationView})`,
      component: () => import("../views/pages/authentication/authentication-view"),
      meta: { type: "public", redirectTo: "dashboard" },
    },
    {
      name: "blogs",
      path: `/:blogType(${patternMatch.blogTypes})`,
      component: () => import("../views/pages/blogs/blog-view"),
    },
    {
      name: "dashboards",
      path: "/dashboard",
      component: () => import("../views/pages/dashboards/dashboard-view"),
      meta: { type: "private" },
    },
    {
      name: "users",
      path: `/users/:userTypeCode(${patternMatch.userTypes})/:id(${patternMatch.id})?`,
      component: () => import("../views/pages/users/user-view"),
      meta: { type: "private" },
    },
    {
      name: "products",
      path: `/:productType(${patternMatch.productTypes})/:id(${patternMatch.id})?`,
      component: () => import("../views/pages/products/product-view"),
      meta: { type: "private" },
    },
    {
      name: "segments",
      path: `/segments/:id(${patternMatch.id})?`,
      component: () => import("../views/pages/segments/segment-view"),
      meta: { type: "private" },
    },
    {
      path: "*",
      component: () => import("../views/pages/errors/not-found"),
    },
  ],
});

router.beforeEach((to, from, next) => {
  let authenticated = store.getters.authenticated;
  let routeType = to.meta.type ?? "shared";

  if (!authenticated && routeType == "private") {
    let query = { redirectTo: to.fullPath.substring(1) };
    if (!query.redirectTo) query = null;
    next({ name: "authentication", params: { authenticationView: "sign-in" }, query: query });
  } else if (authenticated && routeType == "public") {
    next({ path: "/" });
  } else {
    next();
  }
});
